import score from "../../components/score/score"
export default {
  data() {
    return {
      animationData: {},
      animationArr: [{}, {}, {}, {}, {}],
      e: {},
      currentId: 0,
      videoContent: "",
      trailer: [{
        "id": "123",
        "poster": "http://122.152.205.72:88/superhero/MARVEL/Avengers3/poster.png",
        "src": "http://122.152.205.72:88/superhero/MARVEL/Avengers3/trailer.mp4"
      }, {
        "id": "456",
        "poster": "http://122.152.205.72:88/superhero/MARVEL/Avengers3/poster.png",
        "src": "http://122.152.205.72:88/superhero/MARVEL/Avengers3/trailer.mp4"
      }],
      mockData: [{
        "movieTitle": "秦时明月之沧海横流",
        "movieScore": 10,
        "movieCategory": "2018 / 美国 / 科幻 / 超级英雄",
        "movieTime": "上映时间：2014-04-04(美国/中国大陆)",
        "cover": "http://122.152.205.72:88/superhero/MARVEL/CaptainAmerica2/cover.png",
        "src": "../../static/icos/praise.png"
      }, {
        "movieTitle": "秦时明月之沧海横流",
        "movieScore": 10,
        "movieCategory": "2018 / 美国 / 科幻 / 超级英雄",
        "movieTime": "上映时间：2014-04-04(美国/中国大陆)",
        "cover": "http://122.152.205.72:88/superhero/MARVEL/CaptainAmerica2/cover.png",
        "src": "../../static/icos/praise.png"
      }, {
        "movieTitle": "秦时明月之沧海横流",
        "movieScore": 10,
        "movieCategory": "2018 / 美国 / 科幻 / 超级英雄",
        "movieTime": "上映时间：2014-04-04(美国/中国大陆)",
        "cover": "http://122.152.205.72:88/superhero/MARVEL/CaptainAmerica2/cover.png",
        "src": "../../static/icos/praise.png"
      }, {
        "movieTitle": "秦时明月之沧海横流",
        "movieScore": 10,
        "movieCategory": "2018 / 美国 / 科幻 / 超级英雄",
        "movieTime": "上映时间：2014-04-04(美国/中国大陆)",
        "cover": "http://122.152.205.72:88/superhero/MARVEL/CaptainAmerica2/cover.png",
        "src": "../../static/icos/praise.png"
      }, {
        "movieTitle": "秦时明月之沧海横流",
        "movieScore": 10,
        "movieCategory": "2018 / 美国 / 科幻 / 超级英雄",
        "movieTime": "上映时间：2014-04-04(美国/中国大陆)",
        "cover": "http://122.152.205.72:88/superhero/MARVEL/CaptainAmerica2/cover.png",
        "src": "../../static/icos/praise.png"
      }]
    }
  },
  onReady() {
    this.videoContent = uni.createVideoContext('Trailer')
  },
  onHide() {
    // 页面隐藏时暂停视频
    this.videoContent.pause()
  },
  onShow() {
    if (this.videoContent) {
      this.videoContent.play()
    }
  },
  onUnload() {
    this.animationData = {}
    this.animationArr = [{}, {}, {}, {}, {}]
  },
  onLoad() {

    this.animation = uni.createAnimation()

    uni.getSystemInfo({
      success: (res) => {
        this.e = res
        console.log(JSON.stringify(res));
      }
    });
    uni.setNavigationBarColor({
      frontColor: '#000000',
      backgroundColor: '#DD524D',
      animation: {
        duration: 400,
        timingFunc: 'easeIn'
      }
    })
  },
  methods: {
    playing(e) {
      let currentId = e.currentTarget.dataset.id
      this.videoContent = uni.createVideoContext(currentId)
      let trailer = this.trailer
      for (let i = 0; i < trailer.length; i++) {
        let temp = trailer[i].id
        if (temp !== currentId) {
          uni.createVideoContext(temp).pause()
        }
      }
    },
    previewPicture() {
      let url = "../../static/carousel/batmanvssuperman.png"
      let arr = []
      arr.push(url)
      uni.previewImage({
        urls: arr,
        success: function(res) {
        }
      })
    },
    praise(e) {

      let gIndex = e.currentTarget.dataset.index
      this.animation.translateY(-60).opacity(1).step({
        duration: 400
      })
      this.animationData = this.animation
      this.animationArr[gIndex] = this.animationData.export()
      setTimeout(() => {
        this.animation.translateY(0).opacity(0).step({
          duration: 0
        })
        this.animationData = this.animation
        this.animationArr[gIndex] = this.animationData.export()
      }, 500)

    }
  },
  components: {
    score
  }
}
