export default {
  name: "score",
  data() {
    return {
      yellowStar: 0,
      greyStar: 5
    }
  },
  props: {
    movieScore: 0,
    showNum: {
      type: Boolean
    }
  },
  created() {
    var tempScore = 0
    // if(this.movieScore!==""&&this.movieScore==undefined&&movieScore!==null){
    tempScore = this.movieScore
    // }
    var yellowStar = parseInt(tempScore / 2)
    var greyStar = 5 - yellowStar
    this.yellowStar = yellowStar
    this.greyStar = greyStar
  }
}
